.slick-prev:before {
  content: none;
}

.slick-next:before {
  content: none;
}

.slick-next {
  right: -5px;
}

.hide-h1-tagline{
  position: absolute;
  left: -99999em;
};

@media only screen and (max-width: 575px){
  .slick-prev {
    left: -20px;
    img {
      width: 30px;
    }
  }
  .slick-next {
    right: -10px;
    img {
      width: 30px;
    }
  }
}
@media only screen and (min-width: 576px){
  .slick-prev {
    left: -40px;
    top: 40% !important;
  }
  .slick-next {
    right: -20px;
    top: 40% !important;
  }
}

.odd-even-section{
  @media only screen and (max-width: 575px){
    .slick-prev {
      left: 0;
    }
    .slick-next {
      right: 20px;
    }
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px){
  .slick-prev {
    left: -40px;
  }
  .slick-next {
    right: -15px;
  }
}

.banner-main {
  .banner-slider-content {
    position: relative;
    height: auto;
    width: 100%;
    img{
      object-fit: fill;
      height: auto;
      max-height: 340px;
      border-radius: 20px;
      @media screen and (max-width: 575px){
        object-position: center;
      }
    }

    .banner-detail {
      position: absolute;
      top: 50%;
      left: 50px;
      transform: translate(50px, -50%);
    }
  }

  .slick-dots {
    bottom: 15px;
    li {
      width: 30px;
      button {
        background-color: var(--secondary);
        height: 8px;
        width: 8px;
        padding: 3px;
        border-radius: 50%;
        display: inline-block;
        &:before {
          content: none;
        }
      }
    }
    .slick-active {
      button {
        background-color: var(--yellow);
        height: 0;
        width: 30px;
        border-radius: 15px;
      }
    }
  }
}

.our-trusted-brands-main {
  background-image: url("../../assets/imgs/dots.svg"), url("../../assets/imgs/dots.svg");
  background-repeat: no-repeat;
  background-position: -15% 30px, 110% 95%;
  border-radius: 0 80px 0 80px;
  position: relative;
  &:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 100px;
    width: 100px;
    background-color: var(--dark-section);
    z-index: -1;
  }
  .brand-detail {
    width: 180px;
    height: 125px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--white);
    border-radius: 10px;
    box-shadow: 0px 6px 25px #00000008;
    img{
      width: auto;
      object-fit: contain;
      height: 100px;
    }
    @media only screen and (max-width: 575px) {
      width: 100%;
      height: 100px;
    }
    @media only screen and (min-width: 1025px) and (max-width: 1299px) {
      width: 190px;
      img{
        width: 100%;
      }
    }
  }
}

.odd-even-section {
  @media screen and (max-width: 760px){
    .slider-container{
      padding: 40px 40px 0;
    }
  }
  @media screen and (max-width: 480px){
    .whey-product-card{
      width: 130px;
    }
  }
  @media screen and (max-width: 350px){
    .whey-product-card{
      width: 110px;
      padding: 10px !important;
    }
  }
  @media screen and (max-width: 580px) {
    .slick-slider{
      .slick-arrow img{
        height: 30px;
      }
      .slick-track {
        left: 4px;
      }
      .slick-prev{
        left: -25px
      }
      .slick-next{
        right: -25px
      }
    }
    .slider-container{
      padding: 0 38px 0 30px;
    }
  }

  .slick-list {
    padding-top: 50px;
  }

  section:nth-child(odd) {
    background-color: var(--dark-section);
    border-radius: 0 0 80px 0;
    position: relative;
  }

  section:nth-child(even) {
    background-color: var(--yellow);
    border-radius: 0 0 80px 0;
    position: relative;

  }
  .newly-added-main {
    background-image: url("../../assets/imgs/dots.svg"), url("../../assets/imgs/dots.svg");
    background-repeat: no-repeat;
    background-position: -15% 50%, 120% 50%;
  }

  .recently-viewed-main {
    background-image: url("../../assets/imgs/dark-dots.svg"), url("../../assets/imgs/dark-dots.svg");
    background-repeat: no-repeat;
    background-position: 50% -180%, 115% 200%;
  }

  .yellow-before{
    position: relative;
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      height: 100px;
      width: 100px;
      background-color: var(--yellow);
      z-index: -1;
    }
  }

  .dark-before{
    position: relative;
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      height: 100px;
      width: 100px;
      background-color: var(--dark-section);
      z-index: -1;
    }
  }

  .white-before{
    position: relative;
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      height: 100px;
      width: 100px;
      background-color: var(--white);
      z-index: -1;
    }
  }
}

