.product-tabination {
  .product-tab-sidebar {
    background-color: var(--white);
    position: sticky !important;
    @media only screen and (max-width: 1919px) {
      height: calc(100vh - 140px);
    }
    @media only screen and (min-width: 1920px) {
      height: calc(100vh - 140px);
    }
    left: 0;
    top: 140px;

    img {
      width: 40px;
    }
  }

  ul {
    list-style: none;

    li {
      padding: 15px;
    }
    .isCurrent {
      padding: 15px;
      background: #F7E4F3;
    }
    .isCurrent a, h2 {
      color: #f1a07a;
    }
  }
}

